import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NewBidClientComponent } from './new-bid-client/new-bid-client.component';

const routes: Routes = [
  {   path: '',component : NewBidClientComponent,}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BidModuleRoutingModule { }
